.card1{
  background: rgb(34,193,195);
  background: linear-gradient(0deg, rgba(34,193,195,1) 0%, rgb(160, 7, 125) 100%);
  height: 40vh;
  width: 70%;
  border-radius: 10px;
  margin-left: 10px;

  margin-top: 20px;
}

.card1 input{
  margin: 10px;
}

._card__details{
display: flex;
border: none;
background-color: transparent;
color: transparent;
text-shadow: 0 0 0 #000;
}

._card__details input{
  width: 80px;
}

.cardinput{
  margin-top: 30px;
 
}

.expiry{
  display: flex;
  justify-content: space-between;

}
.expiry input{
  width: 30px;
}


#input{
   border: none;
background-color: transparent;
color: transparent;
text-shadow: 0 0 0 #000; 
border-radius: 5px;
height: 30px;
}  

.slash{
  display: inline-block;
  padding-top: 20px;
  
}
._card__number input{
  margin-top: 100px;
  font-size: larger;
  max-width: 300px;

}